import React, {Component} from 'react';

///////////////////////////////////////
// CLASS: NAVBAR
///////////////////////////////////////
interface NavbarProps {
  clearGrid: any;
  visualizeDijkstra: any;
  generateRandomMaze: any;
  visualizeAstar?: any;
  generateScatterRandom: any;
  changeAnimationSpeed: any;
}

export class Navbar extends Component<NavbarProps> {
  render() {
    let clearGrid = this.props.clearGrid;
    let visualizeDijkstra = this.props.visualizeDijkstra;
    let generateRandomMaze = this.props.generateRandomMaze;
    //let visualizeAstar = this.props.visualizeAstar;
    let generateScatterRandom = this.props.generateScatterRandom;
    let changeAnimationSpeed = this.props.changeAnimationSpeed;

    return (
      <div className="navbar-wrapper">
        <nav className="navbar navbar-dark navbar-expand justify-content-center">
          <div className="container">
            <ul className="navbar-nav nav-justified w-100 text-center mt-1">
              <NavbarIcon
                icon="map-marker"
                iconTitle="Dijkstra's Algorithm"
                iconTitleID="visualizeDijkstrasIcon"
                iconFunction={() => visualizeDijkstra()}
              />
              <NavbarIcon
                icon="question"
                iconTitle="Generate Scatter"
                iconTitleID="generateScatterIcon"
                iconFunction={() => generateScatterRandom()}
              />
              <NavbarIcon
                icon="fast-forward"
                iconTitle="Change Speed"
                iconID="changeSpeedIcon"
                iconTitleID="changeSpeedTitle"
                iconFunction={() => changeAnimationSpeed()}
              />
              <NavbarIcon
                icon="microchip"
                iconTitle="New Maze (W.I.P)"
                iconFunction={() => generateRandomMaze()}
              />
              <NavbarIcon
                icon="th"
                iconTitle="Clear Grid"
                iconFunction={() => clearGrid()}
              />
            </ul>
          </div>
        </nav>
      </div>
    );
  }
}

///////////////////////////////////////
// CLASS: NAVBARICON
///////////////////////////////////////

interface NavbarIconProps {
  icon: string;
  iconTitle: string;
  iconTitleID?: string;
  iconID?: string;
  iconFunction: any;
}

class NavbarIcon extends Component<NavbarIconProps> {
  render() {
    const icon = this.props.icon;
    const iconTitle = this.props.iconTitle;
    const iconTitleID = this.props.iconTitleID;
    const iconID = this.props.iconID;
    const iconClass = `fa fa-${icon} fa-lg`;
    const iconFunction = this.props.iconFunction;

    return (
      <li className="nav-item">
        <a
          href="#!"
          className="nav-link d-flex flex-column"
          data-toggle="collapse"
          onClick={iconFunction}>
          <span id={iconID} className={iconClass}></span>
          <span id={iconTitleID} className="d-none d-sm-inline mt-1">
            {iconTitle}
          </span>
        </a>
      </li>
    );
  }
}
