const newMaze = (grid: any) => {
  //set all the nodes to walls
  for (let i = 0; i < grid.length; i++) {
    for (let j = 0; j < grid[i].length; j++) {
      if (grid[i][j].isStart === false && grid[i][j].isFinish === false) {
        grid[i][j].isWall = true;
      }
    }
  }

  let currentNode = grid[0][0];
  // while there are unvisited nodes
  while (getUnvisitedNeighbors(currentNode, grid).length > 0) {
    // choose one of the unvisited neighbors
    // remove the wall between the current node and that neighbor
    const randomUnvisitedNeighbor = getUnvisitedNeighbors;
    // make the neighbor the current node
    currentNode = randomUnvisitedNeighbor;
  }
};

function getUnvisitedNeighbors(node: any, grid: Array<any>) {
  const neighbors = [];
  const {col, row} = node;
  if (row > 0) neighbors.push(grid[row - 1][col]);
  if (row < grid.length - 1) neighbors.push(grid[row + 1][col]);
  if (col > 0) neighbors.push(grid[row][col - 1]);
  if (col < grid[0].length - 1) neighbors.push(grid[row][col + 1]);
  return neighbors.filter(neighbor => !neighbor.isVisited);
}

export default newMaze;
